body{
    background-color: #FFC4EF;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.container{
    display: flex;
    align-items: center;
    background-color:#FF4ACD;
    color: #FFFFFF;
    border-radius: 25px;
    span{
        font-weight: bold;
        color: black;
    }
    h1{
        padding-left: 100px;
        padding-top: 25px;
        padding-bottom:25px ;
    }
    img{
        padding-left: 1050px;
    }
}
.showcase{
    display: flex;
    justify-content: space-between;

    .image{
        border-radius: 25px;
        background-color: #FFFFFF;
        margin-top: 20px;
        padding-bottom: 10px;
        width: 18%;
        img{
            width: 200px;
            height: 200px;
            padding-top: 10px;
            margin-left: 50px;
            margin-top: 10px;
        }
        h3{
            padding-left: 65px;
        }
        h4{
            padding-left: 90px;
        }
        a{
            text-decoration: none;
            text-transform: none;
            color: black;
            padding-left: 65px;
        }
        a:hover{
            color: black;
            font-weight: bold;
        }
    }

}
footer{
    margin-top: 180px;
    padding-top: 15px ;
    padding-bottom: 15px;
    font-size: 10px;
}